<template>
  <b-modal title="Search Address" size="lg" v-model="showModal" @shown="handleOpen">
    <div class="geocodeMap" id="viewDiv">
    </div>
    <b-btn slot="modal-footer" block @click="saveResult" variant="primary">Save</b-btn>
  </b-modal>
</template>

<script>
import {sync} from 'vuex-pathify'

export default {
  data: function() {
    return {
      geometry: {
        location: "",
        x: "",
        y: ""
      }
    }
  },
  computed: {
    showModal: sync('geocodeModal')
  },
  //initialize map on element mount
  mounted: function() {
    const m = this.$store.state.esriModules;
    Promise.all([m.Map,m.MapView,m.Search]).then(([[Map],[MapView],[Search]])=>{
      const map = new Map({
        basemap: 'streets-navigation-vector'
      });

      var view = new MapView({
        map: map,
        container: 'viewDiv',
        center: [-79.3854,43.64869],
        zoom: 10
      });

      this.mysearch = new Search({
        view:view
      });

      view.ui.add(this.mysearch, 'top-right');

      this.mysearch.on('select-result',(event)=> {
        this.geometry.location = event.result.name;
        this.geometry.x = event.result.feature.geometry.longitude;
        this.geometry.y = event.result.feature.geometry.latitude;
      });
    });
  },
  methods: {
    //save results to the store
    saveResult: function() {
      this.showModal = false;
      let dir = this.$store.get('geocodeReq');
      this.$store.set(dir, JSON.parse(JSON.stringify(this.geometry)))
    },
    //on modal open clear previous search results
    handleOpen: function() {
      this.mysearch.clear();
      this.mysearch.focus();
    }
  }
}
</script>

<style>
#viewDiv {
  height: 70vh;
}
</style>